/*Header active nav icon added*/
nav .ACTIVENAV{
	position: relative;
}
nav .ACTIVENAV:before{	
	background-image: url('../images/ktktlogo.png');
	content: "";
	height: 40px;
	width: 19px;
	right: 0;
	top: -14px;
	position: absolute;
	background-size: contain;
	background-repeat: no-repeat;
}

.main-logo img{
	width: 120px;
}
/*Header Setting icons added*/
#open-settings-dialog-button:before{
	background-image: url('../images/cogs.png');
	content: "";
	height: 23px;
	width: 23px;
	position: absolute;
	top: 6px;
	left: 8px;
	background-size: contain;
	background-repeat: no-repeat;
	transform: rotate(19deg);
}
#open-settings-dialog-button svg circle{
	stroke: transparent;
}
#connect-wallet:before{
	background-image: url('../images/candy-1.png');
	content: "";
	height: 33px;
	width: 33px;
	position: absolute;
	top: 4px;
	right: 4px;
	background-size: contain;
	background-repeat: no-repeat;
}



