	/*background-image: url('../images/bas-home.png');*/
	/*background-repeat: no-repeat;
	background-size: contain;*/
	min-height: 100vh;
	width: 1280px;
	position: relative;
}
.parent-balance img{
	width: 100%;
}

.chocolady{
	/*position: relative;*/
}
.chocolady .img-parent{
  position: absolute;
  left: 168px;
  top: 73px;

}
.chocolady .img-parent img{
	width: 150px;
}

.machine-names{
	color: white;
	position: absolute;	
  top: 11vh;
  left: 12vw;
	white-space: nowrap;
	width: 120px;
}


.ktkt-price{
	text-align: center;
	position: absolute;
	top: 173px;
	left: -55px;
}
.mms-price{
	text-align: center;
	position: absolute;
	top: 173px;
	left: 32px;
}
.right-mirror{
	position: absolute;
	top: -88px;
	right: 80px;

}
.big-mirror {
  position: relative;
  z-index: 99;
}
.right-mirror .big-mirror img{
	width: 410px;
}


.right-mirror .big-mirror .big-mirror-text{
  text-align: center;
  color: white;
  position: absolute;
  top: 110px;
  width: 410px;
}
.right-mirror .small-mirrors{
	display: flex;
	justify-content: space-around;
	position: relative;
	top: -20px;
	z-index: 9;
}

.right-mirror .small-mirrors .small-mirror-1 img, .right-mirror .small-mirrors .small-mirror-2 img{
	width: 160px;
}

.small-mirror-1, .small-mirror-2{
	position: relative;
}

.small-mirror-1 .small-mirror-1-text, .small-mirror-2 .small-mirror-2-text{
	color: white;
	width: 160px;
	text-align: center;
	position: absolute;
	bottom: 20px;
}
.bottom-btn-parent{
	position: relative;
}
.bottom-btn{
	width: calc(100% + 134px);
	border-bottom: 6px solid yellow;
	position: absolute;
	left: -70px;
	bottom: 0;
}
.btn-child{
  display: flex;
  justify-content: space-around;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -36px;
	width: calc(60vw);
	margin: auto;
}
.jut-parent-btn-img{
	position: relative;
}
.jut-parent-btn-img img{
	width: 150px;
}
.btn-red-text{
	color: white;
	position: absolute;
	width: 150px;
	bottom: 23px;
	text-align: center;
	font-weight: 600;
}

.bottom-pool{
	background-image: url('../images/bottom-screen.png');
	background-size: cover;
	background-position: top;
}

.swap-ex{
	max-width: 100%;
	width: 100%;
	background-image: url('../images/base-exchange.jpg');
	min-height: 100vh;
	background-size: cover;
}
.inner-child-swap{
	max-width: 420px;
	background-color: #794228;
	margin: 40px 60px;
	padding: 30px;
	border-radius: 40px;
	margin-top: 170px;
}

.inner-child-swap:before{
	content:'';
	background-image: url('../images/chocoloompa.png');
  background-position: bottom;
  width: 110px;
  height: 210px;
  position: absolute;
  top: 113px;
	background-size: contain;
	background-repeat: no-repeat;
}

#pool-nav-link{
	color: #ccc;
}
.inner-child-swap .ACTIVE{
	color: white !important;
}






